import { Injectable } from "@angular/core";
import {
  AngularFirestore,
  AngularFirestoreCollection
} from "angularfire2/firestore";
import { Observable } from "rxjs";
import "rxjs/add/operator/map";

@Injectable()
export class FirebaseProvider {

  data: any;
  constructor(private afs: AngularFirestore) {
  }

  //Save user on firestore
  saveUser = data =>
    this.afs
      .collection("Users")
      .doc(data.$key)
      .update(data);

  //Create user on firestore
  postUser = data =>
    this.afs
      .collection("Users")
      .doc(data.uid)
      .set(data);

  //Create order on firestore
  postOrder = data => this.afs.collection("Orders").add(data);

  //Get current user from uid
  getCurrentUser = uid => {
    const collection: AngularFirestoreCollection<any> = this.afs.collection(
      "Users",
      ref => ref.where("uid", "==", uid)
    );
    const collection$: Observable<any> = collection
      .snapshotChanges()
      .map(actions => {
        return actions.map(action => ({
          $key: action.payload.doc.id,
          ...action.payload.doc.data()
        }));
      });
    return collection$;
  };

  //Get assuntos
  getAssuntos = () => {
    const collection: AngularFirestoreCollection<any> = this.afs.collection(
      "Assuntos"
    );
    const collection$: Observable<any> = collection
      .snapshotChanges()
      .map(actions => {
        return actions.map(action => ({
          $key: action.payload.doc.id,
          ...action.payload.doc.data()
        }));
      });
    return collection$;
  }
  //Get Questões
  getQuestoes = () => {
    const collection: AngularFirestoreCollection<any> = this.afs.collection(
      "Questoes"
    );
    const collection$: Observable<any> = collection
      .snapshotChanges()
      .map(actions => {
        return actions.map(action => ({
          $key: action.payload.doc.id,
          ...action.payload.doc.data()
        }));
      });
    return collection$;
  }
  getEpisodeById(id: string) {
    if (this.data) {
      return Promise.resolve(this.data);
    }

    return new Promise(resolve => {
      this.afs.collection ("Assuntos")
	    .snapshotChanges()
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        });
    });
  }

}



